@charset "UTF-8";
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Medium.ttf");
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.ttf");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Bold.ttf");
  font-weight: 700;
  font-display: swap;
}
:root {
  --atom-size: 40px;
  --atom-color-hex: #E42C64;
  --atom-color-rgb: 228, 44, 100;
  --nucleus-size: calc(var(--atom-size) / 5);
  --electron-color-hex: #E42C64;
  --electron-size: calc(var(--atom-size) / 25);
  --electron-orbit-size: calc(var(--atom-size) / 2.5);
  --electron-speed: 1.2s;
  --electron-speed-alpha: 1s;
  --electron-speed-omega: .8s;
}

.left-custom-gap {
  left: 24px;
}

.top-custom-gap {
  top: 24px;
}

.right-custom-gap {
  right: 24px;
}

.bottom-custom-gap {
  bottom: 24px;
}

.my-pink-bg {
  background-color: #ff4c82;
  box-shadow: 0 2px 8px #ff4c82;
}

.details-color {
  color: #ff4c82;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "OpenSans";
  color: #FAEDF0;
}

body {
  overflow: overlay;
}

#app {
  overflow: hidden;
}

.app-container {
  background-color: #000000;
}

#enter-button {
  color: #ff4c82;
  font-weight: 700;
  font-size: 18px;
  transition: all 0.5s ease-in-out;
}

.canvas-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(45deg, #1d1e22, #121317);
  background: linear-gradient(45deg, #000000, #000000);
}

.gradient {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: all 2s ease-in-out;
  opacity: 0;
  pointer-events: none;
}

.gradientOn {
  opacity: 1;
  background: radial-gradient(at 50% -150%, #121B74 10px, 15%, rgba(0, 0, 0, 0) 75%);
}

@keyframes colorChange {
  to {
    transform: translateX(50%);
  }
}
@-webkit-keyframes colorChange {
  to {
    -webkit-transform: translateX(50%);
  }
}
@keyframes fadeAway {
  0% {
    visibility: visible;
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes fadeAway {
  0% {
    visibility: visible;
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
  z-index: 30;
  animation: fadeAway 1s forwards ease-in;
}
.mask .svs {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.atom {
  position: fixed;
  z-index: 5;
  width: var(--atom-size);
  height: var(--atom-size);
  animation: 8s atom infinite cubic-bezier(1, 0.25, 0, 0.75);
  top: 24px;
  left: 24px;
}
@media screen and (min-width: 768px) {
  .atom {
    top: 0;
    left: 0;
    position: relative;
  }
}

@keyframes atom {
  0% {
    transform: rotate(0deg) scale(1);
  }
  12.5% {
    transform: rotate(-45deg) scale(0.9);
  }
  25% {
    transform: rotate(-90deg) scale(1);
  }
  37.5% {
    transform: rotate(-135deg) scale(0.9);
  }
  50% {
    transform: rotate(-180deg) scale(1);
  }
  62.5% {
    transform: rotate(-225deg) scale(0.9);
  }
  75% {
    transform: rotate(-270deg) scale(1);
  }
  87.5% {
    transform: rotate(-315deg) scale(0.9);
  }
  100% {
    transform: rotate(-360deg) scale(1);
  }
}
@-webkit-keyframes atom {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
  }
  12.5% {
    -webkit-transform: rotate(-45deg) scale(0.9);
  }
  25% {
    -webkit-transform: rotate(-90deg) scale(1);
  }
  37.5% {
    -webkit-transform: rotate(-135deg) scale(0.9);
  }
  50% {
    -webkit-transform: rotate(-180deg) scale(1);
  }
  62.5% {
    -webkit-transform: rotate(-225deg) scale(0.9);
  }
  75% {
    -webkit-transform: rotate(-270deg) scale(1);
  }
  87.5% {
    -webkit-transform: rotate(-315deg) scale(0.9);
  }
  100% {
    -webkit-transform: rotate(-360deg) scale(1);
  }
}
/* Nucleus */
.atom::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: var(--nucleus-size);
  height: var(--nucleus-size);
  margin-top: calc(var(--nucleus-size) / -2);
  margin-left: calc(var(--nucleus-size) / -2);
  background: var(--electron-color-hex); /* var(--atom-color-hex); */
  border-radius: 100%;
  box-shadow: 0 0 3px 0 var(--atom-color-hex);
  animation: 2s nucleus infinite cubic-bezier(0.65, 0, 0.35, 1);
}

@keyframes nucleus {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.9);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.85);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes nucleus {
  0% {
    -webkit-transform: scale(1);
  }
  25% {
    -webkit-transform: scale(0.9);
  }
  50% {
    -webkit-transform: scale(1);
  }
  75% {
    -webkit-transform: scale(0.85);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
/* Electron Orbit */
.atom > [class^=electron] {
  border-top: solid rgba(var(--atom-color-rgb), 0.45) 1px;
  border-right: solid rgba(var(--atom-color-rgb), 0.35) 2px;
  border-bottom: solid rgba(var(--atom-color-rgb), 0.3) 4px;
  border-left: solid rgba(var(--atom-color-rgb), 0) 2px;
  border-radius: 100%;
  width: 100%;
  height: var(--electron-orbit-size);
  position: absolute;
  top: 50%;
  margin-top: calc(var(--electron-orbit-size) / -2);
}

.atom > .electron-alpha {
  transform: rotate(60deg);
}

.atom > .electron-omega {
  transform: rotate(-60deg);
}

/* Electron Unit */
.atom [class^=electron]::after {
  content: "";
  display: block;
  width: var(--electron-size);
  height: var(--electron-size);
  background: var(--electron-color-hex);
  border-radius: 50%;
  margin-top: calc(var(--electron-size) / -2);
  position: absolute;
  top: 50%;
  left: calc(var(--electron-size) / -1);
  transform: scale(1);
  animation: calc(var(--electron-speed) * 2) electron infinite ease-in-out;
}

.atom .electron-alpha::after {
  animation: calc(var(--electron-speed-alpha) * 2) electron infinite ease-in-out;
}

.atom .electron-omega::after {
  animation: calc(var(--electron-speed-omega) * 2) electron infinite ease-in-out;
}

@keyframes electron {
  0% {
    transform: scale(1);
  }
  12.5% {
    transform: scale(1.5);
  }
  25% {
    transform: scale(1);
  }
  37.5% {
    transform: scale(0.25);
  }
  50% {
    transform: scale(1);
  }
  62.5% {
    transform: scale(1.5);
  }
  75% {
    transform: scale(1);
  }
  87.5% {
    transform: scale(0.25);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes electron {
  0% {
    -webkit-transform: scale(1);
  }
  12.5% {
    -webkit-transform: scale(1.5);
  }
  25% {
    -webkit-transform: scale(1);
  }
  37.5% {
    -webkit-transform: scale(0.25);
  }
  50% {
    -webkit-transform: scale(1);
  }
  62.5% {
    -webkit-transform: scale(1.5);
  }
  75% {
    -webkit-transform: scale(1);
  }
  87.5% {
    -webkit-transform: scale(0.25);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@keyframes turnIt {
  100% {
    transform: rotate3d(1, 1, 1, 360deg);
  }
}
@-webkit-keyframes turnIt {
  100% {
    -webkit-transform: rotate3d(1, 1, 1, 360deg);
  }
}
.container {
  transition: all 1s ease-in-out;
}

.faded-right {
  transform: translateX(200%);
  opacity: 0;
}

.faded-left {
  transform: translateX(-200%);
  opacity: 0;
}

.faded {
  opacity: 0;
}

.social-links-nav, .links-nav {
