}

.social-links-nav li, .links-nav li {
  margin: 6px 0;
}

.links-nav {
  top: 0;
  position: fixed;
  right: 0;
  transition: all 0.4s ease-in-out;
}
@media screen and (max-width: 768px) {
  .links-nav {
    width: 0;
  }
  .links-nav.open {
    width: 100vw;
  }
}
@media screen and (min-width: 768px) {
  .links-nav {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 36px;
  }
  .links-nav.background {
    background-color: #121B74;
    padding: 6px 36px;
    font-size: 12px;
