  }
}
.links-nav .menu-toggle {
  background-color: #ff4c82;
  border-radius: 50%;
  box-shadow: 0 2px 8px #ff4c82;
  cursor: pointer;
  height: 56px;
  right: 24px;
  position: fixed;
  top: 24px;
  width: 56px;
  z-index: 20;
}
.links-nav .menu-toggle-bar {
  background-color: #ffffff;
  bottom: 0;
  height: 2px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  width: 16px;
  top: 0;
  transition: all 0.3s ease-in-out;
}
.links-nav .menu-toggle-bar:first-child {
  transform: translate3d(0, -4px, 0);
}
.links-nav .menu-toggle-bar:last-child {
  transform: translate3d(0, 4px, 0);
}
.links-nav .menu-toggle.toggled {
  background-color: #fff;
}
.links-nav .menu-toggle.toggled .menu-toggle-bar:first-child {
  transform: rotate(135deg) translate3d(0, 0, 0);
  background-color: #ff4c82;
}
.links-nav .menu-toggle.toggled .menu-toggle-bar:last-child {
  transform: rotate(-135deg) translate3d(0, 0, 0);
  background-color: #ff4c82;
}
.links-nav .menu-toggle.toggled .menu-toggle-bar:nth-child(2) {
  opacity: 0;
}
.links-nav ul {
  height: 100vh;
  background: black;
  background-color: #ff4c82;
  transform: translateX(100%);
  opacity: 0;
  width: 100vw;
  height: 100vh;
  padding: 80px 20px;
}
@media screen and (min-width: 768px) {
  .links-nav ul {
    opacity: 1;
    top: 0;
    right: 0;
    transform: none;
    background: transparent;
    width: fit-content;
    height: fit-content;
    padding: 0;
  }
}
.links-nav ul.open {
  transform: none;
  opacity: 1;
}
.links-nav ul li {
  transition: all 0.5s ease-in-out;
  margin: 50px;
}
@media screen and (min-width: 768px) {
  .links-nav ul li {
    height: fit-content;
    padding: 0 10px;
    margin: 0 10px;
    font-size: 14px;
  }
  .links-nav ul li:last-of-type {
    padding-right: 0;
    margin-right: 0;
  }
}
.links-nav ul li.resume {
  color: #ff4c82;
  border: 2px solid #ff4c82;
  border-radius: 5px;
  padding: 6px 10px;
}

.links-nav.background li {
  font-size: 12px;
}

